import React, { useState, useEffect } from 'react';
import ReactPlayer from 'react-player';

export type VideoState = {
  state: 'loading' | 'ready' | 'error';
  secondsPlayed: number;
};

type Props = {
  url: string;
  onUpdate: (state: VideoState) => void;
};

const initialState: VideoState = { state: 'loading', secondsPlayed: 0 };

const VideoPlayer = ({ url, onUpdate }: Props) => {
  const [state, setState] = useState(initialState);
  const update = (changes: Partial<VideoState>) => {
    const newState = {
      ...state,
      ...changes,
    };
    setState(newState);
  };

  useEffect(() => {
    setState(initialState);
  }, [url]);

  useEffect(() => {
    onUpdate(state);
  }, [state]);

  return (
    <ReactPlayer
      className="bg-dark"
      width="100%"
      height="100%"
      url={url}
      controls
      progressInterval={10}
      onProgress={(progress) => {
        update({
          secondsPlayed: progress.playedSeconds,
        });
      }}
      onReady={() => {
        update({
          state: 'ready',
        });
      }}
      onError={() => {
        update({
          state: 'error',
        });
      }}
    />
  );
};

export default VideoPlayer;
