import React from 'react';
import InteractiveMap, { Marker } from 'react-map-gl';
import styled from 'styled-components';

const Circle = styled.div`
  width: 5rem;
  height: 5rem;
  border: 3px solid white;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  background: rgba(255, 0, 0, 0.2);
`;

export type LatLng = {
  latitude: number;
  longitude: number;
};

type Viewport = LatLng & {
  zoom: number;
};

type Props = {
  markerLatLng: LatLng;
  onMarkerDrop: (latLng: LatLng) => void;
};

const Map = ({ markerLatLng, onMarkerDrop }: Props) => {
  const [viewport, setViewport] = React.useState<Viewport>({
    latitude: 38.88983478579566,
    longitude: -77.01221343531986,
    zoom: 15.409421399805353,
  });

  return (
    <div className="w-100 h-100 bg-secondary">
      <InteractiveMap
        {...viewport}
        width="100%"
        height="100%"
        onViewportChange={setViewport}
        mapboxApiAccessToken="pk.eyJ1IjoibWlzdGFrZW5tdXN0YXJkIiwiYSI6ImNrbGVkbWtkdzFrOXEyenRrejVyb2Zzc20ifQ.LkFY7eWOE8ryEW_6Kj0f_Q"
        mapStyle="mapbox://styles/mapbox/satellite-v9"
        onClick={(event) => {
          const [longitude, latitude] = event.lngLat;
          onMarkerDrop({ latitude, longitude });
        }}
      >
        <Marker
          {...markerLatLng}
          draggable
          onDragEnd={(event) => {
            const [longitude, latitude] = event.lngLat;
            onMarkerDrop({ latitude, longitude });
          }}
        >
          <Circle />
        </Marker>
      </InteractiveMap>
    </div>
  );
};

export default Map;
