/* eslint-disable no-param-reassign, no-case-declarations,default-case */
import React, { useMemo, useCallback, useRef, useEffect, useState } from 'react';
import { Node, Editor, Transforms, Range, createEditor } from 'slate';
import { withHistory } from 'slate-history';
import {
  Slate,
  Editable,
  ReactEditor,
  withReact,
  useSelected,
  useFocused,
  RenderElementProps,
} from 'slate-react';

import { suspects } from '../../data';

import Portal from './Portal';

// TODO: add more tags
const HASHTAGS: string[] = suspects.flatMap((suspect) => suspect.primaryHashtag ?? []).sort();

const withHashtags = (editor: ReactEditor) => {
  const { isInline, isVoid } = editor;

  editor.isInline = (element) => {
    return element.type === 'hashtag' ? true : isInline(element);
  };

  editor.isVoid = (element) => {
    return element.type === 'hashtag' ? true : isVoid(element);
  };

  return editor;
};

const insertHashtag = (editor: ReactEditor, hashtag: string) => {
  Transforms.insertNodes(editor, { type: 'hashtag', hashtag, children: [{ text: `#${hashtag}` }] });
  Transforms.move(editor);
  Transforms.insertText(editor, ' ');
  Transforms.move(editor);
};

// Slate is missing the `contentEditable` attribute
type RenderElementPropsWithContentEditable = RenderElementProps & {
  attributes: {
    contentEditable?: boolean;
  };
};

const HashtagElement = ({
  attributes: { contentEditable, ...attributes },
  children,
  element,
}: RenderElementPropsWithContentEditable) => {
  const selected = useSelected();
  const focused = useFocused();
  return (
    <span
      {...attributes}
      style={{
        display: 'inline-block',
        borderRadius: '.25rem',
        backgroundColor: '#eee',
        boxShadow: selected && focused ? '0 0 0 2px #B4D5FF' : 'none',
      }}
    >
      <span contentEditable={contentEditable}>#{element.hashtag}</span>
      {children}
    </span>
  );
};

const Element = (props: RenderElementProps) => {
  const { attributes, children, element } = props;
  switch (element.type) {
    case 'hashtag':
      return <HashtagElement {...props} />;
    default:
      return <div {...attributes}>{children}</div>;
  }
};

type Props = {
  value: Node[];
  onChange: (newValue: Node[]) => void;
};

const NotesInput = ({ value, onChange }: Props) => {
  const ref = useRef<HTMLDivElement>(null);
  const [target, setTarget] = useState<Range | undefined>();
  const [index, setIndex] = useState(0);
  const [search, setSearch] = useState('');
  const editor = useMemo(() => withHashtags(withReact(withHistory(createEditor()))), []);

  const suggestions = HASHTAGS.filter((tag) =>
    tag.toLowerCase().startsWith(search.toLowerCase())
  ).slice(0, 10);

  const onKeyDown = useCallback(
    (event) => {
      if (target) {
        switch (event.key) {
          case 'ArrowDown':
            event.preventDefault();
            const prevIndex = index >= suggestions.length - 1 ? 0 : index + 1;
            setIndex(prevIndex);
            break;
          case 'ArrowUp':
            event.preventDefault();
            const nextIndex = index <= 0 ? suggestions.length - 1 : index - 1;
            setIndex(nextIndex);
            break;
          case 'Tab':
          case 'Enter':
            event.preventDefault();
            Transforms.select(editor, target);
            insertHashtag(editor, suggestions[index]);
            setTarget(undefined);
            break;
          case 'Escape':
            event.preventDefault();
            setTarget(undefined);
            break;
        }
      }
    },
    [index, search, target]
  );

  useEffect(() => {
    if (target && suggestions.length > 0) {
      const el = ref.current;
      if (el) {
        const domRange = ReactEditor.toDOMRange(editor, target);
        const rect = domRange.getBoundingClientRect();
        el.style.top = `${rect.top + window.pageYOffset + 24}px`;
        el.style.left = `${rect.left + window.pageXOffset}px`;
      }
    }
  }, [suggestions.length, editor, index, search, target]);

  return (
    <Slate
      editor={editor}
      value={value}
      onChange={(newValue) => {
        onChange(newValue);
        const { selection } = editor;

        if (selection && Range.isCollapsed(selection)) {
          const [start] = Range.edges(selection);
          const wordBefore = Editor.before(editor, start, { unit: 'word' });
          const before = wordBefore && Editor.before(editor, wordBefore);
          const beforeRange = before && Editor.range(editor, before, start);
          const beforeText = beforeRange && Editor.string(editor, beforeRange);
          const beforeMatch = beforeText && beforeText.match(/^#(\w+)$/);
          const after = Editor.after(editor, start);
          const afterRange = Editor.range(editor, start, after);
          const afterText = Editor.string(editor, afterRange);
          const afterMatch = afterText.match(/^(\s|$)/);

          if (beforeMatch && afterMatch) {
            setTarget(beforeRange);
            setSearch(beforeMatch[1]);
            setIndex(0);
            return;
          }
        }

        setTarget(undefined);
      }}
    >
      <Editable
        className="form-control h-auto"
        renderElement={(props) => <Element {...props} />}
        onKeyDown={onKeyDown}
        style={{ minHeight: '4em' }}
      />
      {target && suggestions.length > 0 && (
        <Portal>
          <div
            ref={ref}
            style={{
              position: 'absolute',
              padding: '3px 0',
              background: 'white',
              borderRadius: '.25rem',
              boxShadow: '0 1px 5px rgba(0,0,0,.2)',
            }}
          >
            {suggestions.map((suggestion, i) => (
              <div
                key={suggestion}
                style={{
                  padding: '0 6px',
                  background: i === index ? '#B4D5FF' : 'transparent',
                  cursor: 'pointer',
                }}
                onMouseEnter={() => setIndex(i)}
                onMouseDown={(event) => {
                  event.preventDefault();
                  Transforms.select(editor, target);
                  insertHashtag(editor, suggestions[i]);
                  setTarget(undefined);
                }}
              >
                {suggestion}
              </div>
            ))}
          </div>
        </Portal>
      )}
    </Slate>
  );
};

export default NotesInput;
