import React from 'react';
import { Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';

import { evidence } from '../data';
import { Filters, Toggles } from '../types/Filters';

import EvidenceViewer from './EvidenceMain';
import FilterBox from './FilterBox';
import filterEvidence from './FilterBox/filterEvidence';
import { PATHS } from './Routes';
import { usePageTitle, useQueryState } from './shared/hooks';

const toArray = (value: string | string[] | null | undefined): string[] => {
  if (Array.isArray(value)) {
    return value;
  }
  if (value == null) {
    return [];
  }
  return [value];
};

const MobileOverlay = () => {
  const [open, setOpen] = React.useState(true);

  return (
    (open && (
      <div
        style={{
          zIndex: 100,
          position: 'absolute',
          width: '100%',
          height: '100vh',
          background: 'rgba(255, 255, 255, 0.95)',
          padding: '30px',
        }}
        className="d-sm-none"
      >
        <h2>January 6 Evidence</h2>
        <p>
          You&rsquo;ll probably find the evidence explorer easier to use on a larger device or in a
          bigger window.
        </p>
        <ul>
          <li>
            <Link
              to={PATHS.evidence}
              onClick={(event) => {
                event.preventDefault();
                setOpen(false);
                return false;
              }}
            >
              Take me to the evidence anyway
            </Link>
          </li>
          <li>
            <Link to={PATHS.suspects}>Persons of Interest</Link>
          </li>
          <li>
            <Link to={PATHS.about}>About</Link>
          </li>
          <li>
            <Link to={PATHS.resources}>Resources for investigators of the Capitol Attack</Link>
          </li>
        </ul>
        <img
          src="/documentation/explorer-overview.jpg"
          style={{ width: '100%', margin: '0px auto' }}
          alt="Example explorer view"
        />
      </div>
    )) ||
    null
  );
};

const EvidenceApp = () => {
  usePageTitle('January 6 Evidence Explorer');

  const [queryState, setQueryState] = useQueryState();

  const filters = {
    location: new Set<string>(toArray(queryState.location)),
    suspect: new Set<string>(toArray(queryState.suspect)),
    otherTags: new Set<string>(toArray(queryState.other_tags)),
  };
  const setFilters = <K extends keyof Filters>(key: K, newValue: Filters[K]): void => {
    setQueryState((prevState) => {
      const queryKey = key === 'otherTags' ? 'other_tags' : key;
      return {
        ...prevState,
        [queryKey]: Array.from(newValue),
      };
    });
  };

  const keywords = toArray(queryState.keywords);
  const setKeywords = (nextKeywords: string[]) => {
    setQueryState((prevState) => ({
      ...prevState,
      keywords: nextKeywords,
    }));
  };

  const [toggles, setToggles] = React.useState<Toggles>({
    includeUntimestamped: true,
  });

  const parsedStartEndTimes =
    typeof queryState.start_end_times === 'string' ? queryState.start_end_times.split(',') : null;
  const startEndTimes: [Date, Date] | null = parsedStartEndTimes
    ? [
        new Date(parseInt(parsedStartEndTimes[0], 10)),
        new Date(parseInt(parsedStartEndTimes[1], 10)),
      ]
    : null;

  const setStartEndTimes = (newTimes: [Date, Date] | null) => {
    // eslint-disable-next-line camelcase
    setQueryState(({ start_end_times, ...prevState }) => ({
      ...prevState,
      ...(newTimes
        ? { start_end_times: [newTimes[0].getTime(), newTimes[1].getTime()].join(',') }
        : {}),
    }));
  };

  const { filteredEvidence, timelineFilteredEvidence, filterChoices } = filterEvidence(
    evidence,
    filters,
    keywords,
    toggles,
    startEndTimes
  );

  return (
    <div>
      <MobileOverlay />
      <FilterBox
        onFilterChanged={setFilters}
        onKeywordsChanged={setKeywords}
        filters={filters}
        choices={filterChoices}
        keywords={keywords}
        toggles={toggles}
        onToggleChanged={(toggle, newValue) => {
          setToggles((oldToggles) => ({ ...oldToggles, [toggle]: newValue }));
        }}
      />
      <EvidenceViewer
        title="January 6 Evidence Explorer"
        evidence={filteredEvidence}
        timedEvidence={timelineFilteredEvidence}
        startEndTimes={startEndTimes}
        setStartEndTimes={setStartEndTimes}
        hideExplorerLink
        rootPath={PATHS.evidence}
        getDetailPath={(detailId) => `${PATHS.evidence}/${detailId}`}
      />
    </div>
  );
};

export default EvidenceApp;
