/* eslint-disable jsx-a11y/label-has-associated-control */
import classNames from 'classnames';
import React, { useState } from 'react';
import { Node } from 'slate';

import { LatLng } from './Map';
import NotesInput from './NotesInput';
import { VideoState } from './VideoPlayer';

const padTime = (num: number) => num.toString().padStart(2, '0');

const toDuration = (seconds: number) => {
  const hour = Math.floor(seconds / 60 / 60);
  const min = Math.floor(seconds / 60);
  const sec = Math.floor(seconds - min * 60 - hour * 60 * 60);
  return `${hour ? `${padTime(hour)}:` : ''}${padTime(min)}:${padTime(sec)}`;
};

export type Annotation = {
  videoUrl: string;
  videoTime: number;
  latLng: LatLng;
  localTime: Date | null;
  notes: string;
  hashtags: string[];
};

type Props = {
  url: string;
  onUrlChange: (url: string) => void;
  videoState: VideoState | null;
  latLng: LatLng;
  onSubmit: (annotation: Annotation) => void;
};

const initialNotes = [
  {
    type: 'paragraph',
    children: [{ text: '' }],
  },
];

const AnnotationForm = ({ url, onUrlChange, videoState, latLng, onSubmit }: Props) => {
  const [localStartTime, setLocalStartTime] = useState<Date | null>(null);
  const [notes, setNotes] = useState<Node[]>(initialNotes);

  const localCurrentTime = localStartTime
    ? new Date(localStartTime.getTime() + Math.floor(videoState?.secondsPlayed ?? 0) * 1000)
    : null;

  const findHashtags = (node: Node) =>
    // @ts-ignore
    node.type === 'hashtag' ? node : node.children?.flatMap(findHashtags);

  return (
    <form
      className="p-3"
      onSubmit={(event) => {
        event.preventDefault();

        onSubmit({
          videoUrl: url,
          videoTime: videoState?.secondsPlayed ?? 0,
          latLng,
          localTime: localCurrentTime,
          notes: notes
            .map((n) => Node.string(n))
            .join('\n')
            .trim(),
          hashtags: notes
            .flatMap(findHashtags)
            .filter(Boolean)
            .map((n) => n.hashtag as string),
        });
        setNotes(initialNotes);
      }}
    >
      <div className="form-row">
        <div className="form-group col-9">
          <label htmlFor="url" className="mb-0">
            Video URL
          </label>
          <input
            className={classNames(
              'form-control',
              videoState?.state === 'error' ? 'is-invalid' : null
            )}
            name="url"
            type="url"
            value={url}
            onChange={(event) => onUrlChange(event.target.value)}
            aria-describedby="urlValidation"
          />
          <div id="urlValidation" className="invalid-feedback">
            Couldn&apos;t load this video. Check the URL and try again?
          </div>
        </div>
        <div className="form-group col-3">
          <label htmlFor="videoTime" className="mb-0">
            Video time
          </label>
          <input
            className="form-control"
            name="videoTime"
            type="text"
            value={toDuration(videoState?.secondsPlayed ?? 0)}
            disabled
          />
        </div>
      </div>
      <div className="form-row">
        <div className="form-group col">
          <label htmlFor="latLng" className="mb-0">
            Location
          </label>
          <input
            className="form-control"
            name="latLng"
            type="text"
            value={`${latLng.latitude}, ${latLng.longitude}`}
            disabled
          />
        </div>
        <div className="form-group col">
          <label htmlFor="localTime" className="mb-0">
            Local time (EST)
          </label>
          <input
            className="form-control"
            name="localTime"
            type="time"
            step={1}
            value={
              localCurrentTime
                ? `${padTime(localCurrentTime.getHours() + 1)}:${padTime(
                    localCurrentTime.getMinutes()
                  )}:${padTime(localCurrentTime.getSeconds())}`
                : ''
            }
            onChange={(event) => {
              const [hours, minutes, seconds] = event.target.value.split(':');
              const date = new Date(2021, 0, 6, +hours - 1, +minutes, +seconds);
              setLocalStartTime(new Date(date.getTime() - (videoState?.secondsPlayed ?? 0) * 1000));
            }}
          />
        </div>
      </div>
      <div className="form-group">
        <label className="mb-0">Notes</label>
        <NotesInput value={notes} onChange={setNotes} />
      </div>
      <div className="d-flex justify-content-end">
        <button type="submit" className="btn btn-primary">
          Save annotation
        </button>
      </div>
    </form>
  );
};

export default AnnotationForm;
